import React from 'react';
import { BrowserRouter as Router } from 'react-router-dom';
import { EntranceProvider } from './hooks/entrance';
import Routes from './routes';
import GlobalStyle from './styles/global';

function App() {
  return (
    <Router>
      <EntranceProvider>
        <Routes />
      </EntranceProvider>
      <GlobalStyle />
    </Router>
  );
}

export default App;
