import React, { useEffect, useState, useCallback } from 'react';
import { useHistory } from 'react-router-dom';

import Title from '../../components/Title';
import Slider from '../../components/Slider';
import BackButton from '../../components/BackButton';
import HeaderLine from '../../components/HeaderLine';
import { useEntrance } from '../../hooks/entrance';

import Modal from './Modal';
import {
  Container,
  GitHubLink,
} from './styles';

const emptyObj = {
  title: '',
  types: '',
  shortDescription: '',
  mainImage: '',
  videos: [],
  images: [],
  links: [],
};

const Projects = () => {
  const [selectedProject, setSelectedProject] = useState(emptyObj);

  const { setPageEntered, isOnMobile } = useEntrance();

  const history = useHistory();

  useEffect(() => {
    setPageEntered(true);
  }, []);

  const onCloseModal = useCallback(() => {
    setSelectedProject(emptyObj);
  }, []);

  return (
    <>
      <BackButton onClickButton={() => history.push('/')}/>
      <Modal 
        show={selectedProject.title.length !== 0}
        data={selectedProject}
        onClose={onCloseModal}
      />
      <Container>
        <Title name="Projects"/>
        {isOnMobile && (
          <HeaderLine color="#63A620"/>
        )}
        <GitHubLink href="https://github.com/gkum4" target="_blank">
          <img src={require('../../assets/github-image.png')} alt="GitHub Logo"/>
        </GitHubLink>

        <Slider setSelectedProject={setSelectedProject}/>
      </Container>
    </>
  );
}

export default Projects;