import React, { useCallback, useEffect, useState } from 'react';
import { Link } from 'react-router-dom';

import Title from '../../components/Title';
import { useEntrance } from '../../hooks/entrance';


import { 
  EntranceContainer,
  Container,
  TitleContainer,
  ButtonContainer
} from './styles';

const Home = () => {
  const { pageEntered } = useEntrance();
  const [entranceContainerVisible, setEntranceContainerVisible] = useState(true);

  return (
    <>
      {!pageEntered && (
        <EntranceContainer 
          onClick={() => setEntranceContainerVisible(false)} 
          visible={entranceContainerVisible}
        >
          <h1>Hello. My name is Gustavo</h1>
          <p>click anywhere</p>
        </EntranceContainer>
      )}
      <Container>
        <Title name="Kuma"/>

        <ButtonContainer lineColor="red">
          <Link to="/about"><h2>About</h2></Link>
          <div></div>
        </ButtonContainer>

        <ButtonContainer lineColor="green">
          <Link to="/projects"><h2>Projects</h2></Link>
          <div></div>
        </ButtonContainer>

        <ButtonContainer lineColor="blue">
          <Link to="/contact"><h2>Contact</h2></Link>
          <div></div>
        </ButtonContainer>
      </Container>
    </>
  );
}

export default Home;