import styled, { keyframes, css } from 'styled-components';

const bounce = keyframes`
  0% {
    transform: translateY(8px);
  }
  100% {
    transform: translateY(-8px);
  }
`;

const bouceHorizontal = keyframes`
  0% {
    transform: translateX(8px);
  }
  100% {
    transform: translateX(-8px);
  }
`;

export const EntranceContainer = styled.div`
  display: flex;
  width: 100vw;
  height: 100vh;
  justify-content: center;
  align-items: center;
  position: absolute;
  background-color: rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(10px);
  transition: opacity 0.5s, visibility 0.5s;
  z-index: 100;
  cursor: pointer;

  ${({ visible }) => visible &&
    css`
      visibility: visible;
      opacity: 1;
    `
  }
  ${({ visible }) => !visible &&
    css`
      visibility: hidden;
      opacity: 0;
    `
  }

  h1 {
    font-size: 64px;
    font-weight: bold;
  }

  p {
    font-weight: 300;
    font-size: 14px;
    position: absolute;
    bottom: 50px;
    animation: ${bounce} .5s cubic-bezier(.5, .6, .6, 1) infinite alternate;
  }

  @media(max-width: 480px) {
    padding: 0 30px;

    h1 {
      font-size: 48px;
      font-weight: bold;
      text-align: center;
    }

    p {
      bottom: 90px;
    }
  }
`;

export const Container = styled.div`
  display: flex;
  width: 100vw;
  height: 100vh;
  justify-content: center;
  align-items: center;
  padding: 130px;
  flex-direction: row;

  @media(max-width: 480px) {
    flex-direction: column;
    padding: 51px;
  }
`;

export const ButtonContainer = styled.div`
  display: flex;
  flex: 1;
  height: 100%;
  width: 100%;
  justify-content: center;
  align-items: center;
  position: relative;

  a {
    transition: opacity 0.5s, visibility 0.5s;
    visibility: hidden;
    opacity: 0;
    cursor: pointer;
    position: absolute;
    z-index: 20;
    color: #ebebeb;
    text-decoration: none;
  }

  h2 {
    font-size: 64px;
  }

  &:hover {
    a {
      visibility: visible;
      opacity: 1;
    }
  }

  div {
    display: flex;
    width: 5px;
    height: 100%;
    animation: ${bounce} .5s cubic-bezier(.5, .6, .6, 1) infinite alternate;

    ${({ lineColor }) => {
      if (lineColor === 'red') {
        return css`
          background: #A62020;
        `;
      }
      if (lineColor === 'green') {
        return css`
          background: #63A620;
        `;
      }
      if (lineColor === 'blue') {
        return css`
          background: #203EA6;
        `;
      }
    }}
  }
  
  @media(max-width: 480px) {
    h2 {
      font-size: 48px;
    }

    div {
      display: flex;
      width: 100%;
      height: 5px;
      animation: ${bouceHorizontal} .5s cubic-bezier(.5, .6, .6, 1) infinite alternate;
    }

    a {
      visibility: visible;
      opacity: 1;
    }
  }
`;
