import React, { useEffect } from 'react';
import { useHistory } from 'react-router-dom';

import BackButton from '../../components/BackButton';
import Title from '../../components/Title';
import HeaderLine from '../../components/HeaderLine';
import { useEntrance } from '../../hooks/entrance';

import {
  Container,
} from './styles';

const Contact = () => {
  const { setPageEntered, isOnMobile } = useEntrance();
  const history = useHistory();

  useEffect(() => {
    setPageEntered(true);
  }, []);

  return (
    <>
      <BackButton onClickButton={() => history.push('/')}/>
      <Container>
        <Title name="Contact"/>
        {isOnMobile && (
          <HeaderLine color="#203EA6"/>
        )}

        <a className="email" href="mailto:kumasawa@alunos.utfpr.edu.br">kumasawa@alunos.utfpr.edu.br</a>
        <a className="linkedin" href="https://www.linkedin.com/in/gustavo-kumasawa-0b2252173/" target="_blank">LINKEDIN</a>
        <a className="instagram" href="https://www.instagram.com/gus.kuma/" target="_blank">INSTAGRAM</a>
      </Container>
    </>
  );
}

export default Contact;