import React from 'react';

import {
  Container,
  ModalContainer,
  ScrollContainer,
  LinkContainer,
} from './styles';

const Modal = ({ show, data, onClose }) => {
  return (
    <Container show={show}>
      <ModalContainer>
        <img src={require('../../../assets/close-icon.png')} onClick={onClose}/>
        <h2>{data.title}</h2>
        <span>({data.types})</span>
        <ScrollContainer>
          <p>{data.shortDescription}</p>
          <br/>
          <p>{data.longDescription}</p>
          {data.videos.length !== 0 && data.videos.map(item => (
            <>
              <br/>
              <strong>{item.label}</strong>
              <br/>
              <iframe width="100%" height="auto" src={item.link} frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; fullscreen" ></iframe>
            </>
          ))}
          {data.links.length !== 0 && data.links.map(item => (
            <>
              <br/>
              <LinkContainer>
                <strong>{item.label}</strong>
                <a href={item.link} target="_blank">{item.link}</a>
              </LinkContainer>
            </>
          ))}
          {!!data.mainImage && (
            <>
              <br/>
              <img src={data.mainImage}/>
            </>
          )}
          {data.images.length !== 0 && data.images.map(item => (
            <>
              <br/>
              <strong>{item.label}</strong>
              <br/>
              <img src={item.url}/>
            </>
          ))}
        </ScrollContainer>
      </ModalContainer>
    </Container>
  );
}

export default Modal;