import React, { useCallback, useEffect } from 'react'
import ScrollContainer from 'react-indiana-drag-scroll'

import { useEntrance } from '../../hooks/entrance';
import projectsData from '../../utils/projectsData';

import {
  Container,
  ProjectCard,
  ProjectCardOverlay,
  MobileContainer,
  MobileCard,
  MobileCardOverlay,
  Global,
} from './styles';

const Slider = ({ setSelectedProject }) => {
  const { isOnMobile } = useEntrance();
  if (isOnMobile) {
    return (
      <MobileContainer>
        {projectsData.map(item => (
          <MobileCard onClick={() => setSelectedProject(item)}>
            <MobileCardOverlay>
              <h2>{item.title}</h2>
            </MobileCardOverlay>
            <img src={item.mainImage}/>
          </MobileCard>
        ))}
      </MobileContainer>
    );
  } else {
    return (
      <>
        <ScrollContainer className="scroll-container" hideScrollbars={false} vertical={false}>
          <Container numberOfItems={projectsData.length}>
            {projectsData.map(item => (
              <ProjectCard>
                <ProjectCardOverlay onClick={() => setSelectedProject(item)}>
                  <h2>{item.title}</h2>
                  <span>({item.types})</span>
                  <p>{item.shortDescription}</p>
                  <span>click for more</span>
                </ProjectCardOverlay>
                <img src={item.mainImage}/>
              </ProjectCard>
            ))}
          </Container>
        </ScrollContainer>
        <Global />
      </>
    );
  }
  
}

export default Slider;