import React, { useEffect } from 'react';
import { useHistory } from 'react-router-dom';

import Title from '../../components/Title';
import BackButton from '../../components/BackButton';
import HeaderLine from '../../components/HeaderLine';
import { useEntrance } from '../../hooks/entrance';
import aboutMyself from '../../utils/aboutMyself';

import {
  Container,
  MainTextContainer,
  ImageContainer,
  MainTextHeader,
  TechnologiesContainer,
  TechnologyContainer,
  Global,
} from './styles';

const About = () => {
  const { setPageEntered, isOnMobile } = useEntrance();
  const history = useHistory();

  useEffect(() => {
    setPageEntered(true);
  }, []);

  return (
    <>
      <Container>
        <BackButton onClickButton={() => history.push('/')}/>
        <Title style={{ right: '50px' }} name="About"/>
        {isOnMobile && (
          <HeaderLine color="#A62020"/>
        )}

        <MainTextHeader>
          <h1>I really like challenges.</h1>
          <p>scroll down</p>
        </MainTextHeader>
        <ImageContainer>
          <img src={require('../../assets/me-image.jpg')}/>

        </ImageContainer>

        <MainTextContainer>
          {aboutMyself.text.map(text => (
            <>
              <p>{text}</p>
            </>
          ))}
          <TechnologiesContainer>
            <strong>Technologies that I know: </strong>
            {aboutMyself.technologies.map(item =>
              <TechnologyContainer>
                <span>{item}</span>
              </TechnologyContainer>
            )}
          </TechnologiesContainer>
        </MainTextContainer>

      </Container>
      <Global />
    </>
  );
}

export default About;